/* NEWS & EVENTS MAIN */

#news_and_events_main {
  border: var(--third-color) solid 5px;
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  overflow-y: scroll;

  box-sizing: border-box;
}

/* NEWS & EVENTS MAIN H1 */
#news_and_events_main-div1{
    height: 11vh;
    width: 100%;
    margin-top: 2%;
    display: flex;
  justify-content: center;
  align-items: center;
}

#news_and_events_main-div1-h1 {
  height: 100%;
  width: 20%;
  background-color: var(--fourth-color);
  color: var(--third-color);
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: underline;
  text-decoration-color: var(--third-color);
}

/* NEWS & EVENTS SECTION1 */

#news_and_events_main_section1 {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-bottom: 3%;

    margin-top: 3%;
}

/* NEWS MAIN SECTION1 MAIN NAVBAR */

#news_and_events_main_section1_nav1 {
  border-bottom: none;
  height: 7vh;
  width: 100%;
  z-index: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;

}

/* NEWS MAIN SECTION1 MAIN NAVBAR - BUTTONS*/
#news_and_events_main_section1-section1{

  height: 100%;
  width: 20%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.news_and_events_main_section1-section1-btn{

  height: 100%;
  width: 50%;
  cursor: pointer;
  background-color: white;
  border: none;
  font-size: 105%;
}
.news_and_events_main_section1-section1-btn:hover{
  background-color: var(--fourth-color);
  color: var(--third-color);
}
.news_and_events_main_section1-section1-btn_selected{
  background-color: var(--third-color);
  color: var(--fourth-color);
}

/* NEWS MAIN SECTION1 MAIN NAVBAR - NAVBAR2 */

.news_and_events_main_section1_nav1_ul {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
  height: 100%;
  box-sizing: border-box;
  width: 100%;
  background-color: var(--fourth-color);
  display: none;
}




.news_and_events_main_section1_nav1_ul_li{
  width: 15%;
  height: 100%;
  cursor: pointer;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  list-style: none;
}
.news_and_events_main_section1_nav1_ul_li:last-child{
  border-right: none;
}
.news_and_events_main_section1_nav1_ul_li:hover{
  background-color: var(--third-color);
  color: var(--fourth-color);
}
.news_and_events_main_section1_nav1_ul_li_sort_by{
  border-right: none;
  cursor: default;
}
.news_and_events_main_section1_nav1_ul_li_sort_by:hover{
    background-color: var(--fourth-color);
  color: var(--third-color);
}

.news_and_events_main_section1_nav1_ul_li_p{
  min-height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  margin-top: 0;
  margin-bottom: 0;
}

.news_and_events_main_section1_nav1_ul_li_ul{
  z-index: 2;
  margin-top: 0;

  width: 100%;
  border: solid var(--third-color) 1px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-left: 0;
  display: none;

}

.news_and_events_main_section1_nav1_ul_li_ul_li{

  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin-left: 0;
  background-color: var(--fourth-color);
  color: var(--third-color);
}
.news_and_events_main_section1_nav1_ul_li_ul_li:hover{
  background-color: var(--third-color);
  color: var(--fourth-color);
}


.news_and_events_main_section1_nav1_ul_li_ul_li_p{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}


/* NEWS & EVENTSMAIN SECTION1 ARTICLES MAIN SECTION*/

#news_and_events_main_section1_section1{
  height: auto;
  width: 95%;
  box-sizing: border-box;
  margin-top: 3%;
}

/* LOADING  */
#news_and_events_main_section1_section1_div1_loading{
  height: 60vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
/* NEWS MAIN SECTION1 ARTICLES NEWS SECTION*/
/* NO NEWS */
#news_and_events_main_section1_section1_div1_no_news{
  height: 60vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

#news_and_events_main_section1_section1_div1_no_news_h3{
  padding: 3%;
  background-color: var(--third-color);
  color: var(--fourth-color);
}

/*  NEWS ARTICLES */

#section_news{

}

.section_news_article {
  height: 60vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-bottom: solid var(--main-color) 2px;
  margin-bottom: 2%;
  padding-bottom: 1%;
  border-right: solid 2px var(--main-color);
}
.section_news_article:first-child{
  margin-top: 2%;
}

/* NEWS ARTICLES div1*/
.section_news_article_div1 {
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

}

.section_news_div1_h4 {
  height: 80%;
  width: 25%;
  margin-right: 2.5%;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  border: var(--third-color) 2px solid;
  background-color: var(--fourth-color);
  color: var(--third-color);
}

.section_news_div1_h3 {
  height: 80%;
  width: 70%;
  margin-right: 2.5%;
  font-size: 90%;
  background-color: var(--third-color);
  color: var(--fourth-color);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: scroll;
}

/* NEWS ARTICLES div2*/

.section_news_article_div2{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  width: 100%;
  height: 80%;
  color: var(--third-color);
}

.section_news_article_div2_div1 {
  min-width: 25%;
  height: 100%;
  margin-right: 2%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  box-sizing: border-box;

}

.section_news_article_div2_div1_img {
  margin-top: 3%;
  min-width: 80%;
  min-height: 80%;
  border-right: solid var(--main-color) 10px;
  object-fit: contain;
  object-position: center;
}
.news-main-section1-section1-article-div2-div1-img:hover {
  border-right: solid var(--third-color) 10px;
}

.section_news_article_div2_div1_p{
  width: 80%;
  height: 80%;
  background-color: var(--third-color);
  color: var(--fourth-color);
  display: flex;
  justify-content: center;
  align-items: center;

}

.section_news_article_div2_div1_h5 {
  width: auto;
  min-height: 12%;
  margin-top: 1%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 3%;
  padding-right: 3%;
  background-color: var(--main-color);
  color: var(--fourth-color);
  font-weight: 600;

}

.section_news_article_div2_div2{
  width: 75%;
  padding: 1%;
  overflow-y: scroll;
  font-size: 120%;
  margin-top: 2%;
}
.section_news_article_div2_div2::-webkit-scrollbar {
  width: 12px;
}
/* scroll bar thumb (the actual scroll) */
.section_news_article_div2_div2::-webkit-scrollbar-thumb {
  background: var(--third-color); /* scrollbar color */
}
/* scrroll bar track */
.section_news_article_div2_div2::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */
}

/*  EVENT ARTICLES */
/* NO EVENTS */
#news_and_events_main_section1_section1_div1_no_events{
  height: 60vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

#news_and_events_main_section1_section1_div1_no_events_h3{
  padding: 3%;
  background-color: var(--third-color);
  color: var(--fourth-color);
}

/* EVENT ARTICLES */
#section_events{

}

.section_events_article{
  height: 60vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border-bottom: solid var(--main-color) 2px;
  margin-bottom: 2%;
  padding-bottom: 1%;
  border-right: solid 2px var(--main-color);
}

/* EVENT ARTICLES  div1*/
.section_events_article_div1 {
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

}

.section_events_div1_h4 {
  height: 80%;
  width: 25%;
  margin-right: 2.5%;
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border: var(--third-color) 2px solid;
  background-color: var(--fourth-color);
  color: var(--third-color);
  padding-left: 1%;
}

.section_events_div1_h3 {
  height: 80%;
  width: 70%;
  margin-right: 2.5%;
  font-size: 90%;
  background-color: var(--third-color);
  color: var(--fourth-color);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: scroll;
}

/* EVENT ARTICLES div 2*/
.section_events_article_div2{
  height: 80%;
  width: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.section_events_article_div2_div1{

  height: 100%;
  width: 25%;
  margin-right: 2.5%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding-top: 0px;
}

.section_events_article_div2_div1_h4{
  min-height: 20%;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: var(--fourth-color);
  color: var(--third-color);
  border: solid var(--third-color) 2px;
  margin-top: 0px;
  box-sizing: border-box;
    padding-left: 3%;
}

.section_events_article_div2_div1_div1{
    min-height: 30%;
    width: 100%;
    box-sizing: border-box;
    padding: 2%;
  color: var(--third-color);
  overflow-y: scroll;
  box-sizing: border-box;

}
.section_events_article_div2_div1_div1::-webkit-scrollbar {
  width: 5px;
}
/* scroll bar thumb (the actual scroll) */
.section_events_article_div2_div1_div1::-webkit-scrollbar-thumb {
  background: var(--third-color); /* scrollbar color */
}
/* scrroll bar track */
.section_events_article_div2_div1_div1::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */
}

.section_events_article_div2_div1_div1_span{
  background-color: var(--third-color);
  color: var(--fourth-color);
  padding: 2%;
}

.section_events_article_div2_div1_div1_h3{
  font-size: 100%;
}

.section_events_article_div2_div1_p{
  height: 15%;
  background-color: var(--main-color);
  color: var(--fourth-color);
  display: flex;
  justify-content: flex-start;
align-items: center;
  padding: 2%;
  margin-left: 2%;
}

.section_events_article_div2_div2{
    height: 100%;
  width: 70%;
  margin-right: 2.5%;
    display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  padding: 1%;
  box-sizing: border-box;
}

.section_events_article_div2_div1_span{
  background-color: var(--third-color);
  color: var(--fourth-color);
  padding: 1%;
}


.section_events_article_div2_div1_span_p{
  font-size: 110%;
  overflow-y: scroll;
}
.section_events_article_div2_div1_span_p::-webkit-scrollbar {
  width: 10px;
}
/* scroll bar thumb (the actual scroll) */
.section_events_article_div2_div1_span_p::-webkit-scrollbar-thumb {
  background: var(--third-color); /* scrollbar color */
}
/* scrroll bar track */
.section_events_article_div2_div1_span_p::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */
}







